import { Routes } from '@angular/router';
import { ErrorComponent } from './error/error.component';
import {AuthGuard} from "./auth/auth.gaurd";


export const ROUTES: Routes = [{
   path: '', redirectTo: 'auth', pathMatch: 'full'
  }, {
    path: 'app',   loadChildren: () => System.import('./layout/layout.module')  .then(function(module) {    return module['LayoutModule'];  }), canActivate: [AuthGuard]
  }, {
    path: 'auth', loadChildren: () => System.import('./auth/auth.module')  .then(function(module) {    return module['AuthModule'];  })
  }, {
    path: 'error', component: ErrorComponent
  }, {
    path: '**',    component: ErrorComponent
  }
];



